<template>
    <right-side-component>
        <template v-slot:title>{{title}}</template>
        <template v-slot:form>
            <v-form ref="form" v-model="valid" lazy-validation>
                <v-autocomplete
                    label="Select employee"
                    :items="employeeList"
                    item-text="name"
                    item-value="id"
                    v-model="selectedEmployeeIdComputed"
                    :rules="selectEmployeeRules"
                ></v-autocomplete>
            </v-form>
        </template>
        <template v-slot:buttons>
            <v-btn :disabled="!valid" color="primary" @click="assign()">Assign</v-btn>
            <v-btn color="primary" flat @click="cancel()">Cancel</v-btn>
        </template>
    </right-side-component>
</template>

<script>
import RightSideComponent from './PlacePeopleRightSideTemplate'
import { mapState, mapActions } from 'vuex'

export default {
    data: () => ({
        title: 'Assign desk',
        valid: false,
        selectEmployeeRules: [v => !!v || 'Employee must be selected']
    }),
    created() {
        this.getAllEmployees()
    },
    components: {
        'right-side-component': RightSideComponent
    },
    computed: {
        ...mapState('placePeople', ['employeeList', 'selectedEmployeeId', 'selectedDesk']),
        selectedEmployeeIdComputed: {
            get() {
                return this.selectedEmployeeId
            },
            set(employeeId) {
                this.setSelectedEmployeeId(employeeId)
            }
        }
    },
    methods: {
        ...mapActions('placePeople', ['getAllEmployees', 'setSelectedEmployeeId', 'postDeskEmployee', 'setSelectedDesk', 'clearSelection']),
        assign() {
            if (this.$refs.form.validate()) {
                this.postDeskEmployee({ deskId: this.selectedDesk.id, employeeId: this.selectedEmployeeId })
            }
        },
        cancel() {
            this.clearSelection()
        }
    }
}
</script>

