<template>
  <v-card id="PeopleDataTableCard">
    <v-card-title>
      Employees
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="search"
        label="Search"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>

    <v-data-table
      :headers="headers"
      :items="filteredEmployees"
      :search="search"
      :footer-props="{ showFirstLastPage, showCurrentPage, itemsPerPageOptions }"
      disable-initial-sort
    >
      <template v-slot:items="props">
        <img :src="props.item.picture" class="user-image">
        <td class="text-xs-left">
          <a v-on:click="showUserInfo(props.item.name)" style="color: #db4326"> {{ props.item.name }} </a>
        </td>
        <td class="text-xs-left">{{ props.item.email }}</td>
        <td class="text-xs-left">{{ props.item.discipline }}</td>
        <td class="text-xs-left">{{ props.item.jobRole }}</td>
      </template>
      <template v-slot:no-results>
        <v-alert :value="true" color="error" icon="warning">
          Your search for "{{ search }}" found no results.
        </v-alert>
      </template>
    </v-data-table>

    <v-dialog v-model="dialog" max-width="600px">
      <v-card>
        <v-card-text>
          <v-layout row id="user-info">
            <v-flex xs5>
              <v-layout column id="user-info-left">
                <img :src="employeeInfo.picture" id="user-info-image">
                <h2 id="user-info-name">{{employeeInfo.name}}</h2>
                <p>{{employeeInfo.email}}</p>
                <p>0728798919</p>
              </v-layout>
            </v-flex>
            <v-flex xs3>
              <v-layout column id="user-info-middle">
                <p>Location:</p>
                <ul style="list-style-type:none;">
                  <li><p>Country: </p></li>
                  <li><p>City: </p></li>
                  <li><p>Building: </p></li>
                  <li><p>Floor: </p></li>
                </ul> 
                <p>Job role: </p>
                <p>Delivery unit: </p>
                <p>Discipline: </p>
                <p>Line manager: </p>
              </v-layout>
            </v-flex>
            <v-flex xs4>
              <v-layout column id="user-info-right">
                <ul style="list-style-type:none;">
                  <br><br>
                  <li><p>{{employeeInfo.tenantName}}</p></li>
                  <li><p>{{employeeInfo.locationName}}</p></li>
                  <li><p>{{employeeInfo.buildingName}}</p></li>
                  <li><p>{{employeeInfo.floorName}}</p></li>
                </ul>
                <p>{{employeeInfo.jobRole}}</p>
                <p>{{employeeInfo.deliveryUnit}}</p>
                <p>{{employeeInfo.discipline}}</p>
                <p>{{employeeInfo.lineManager}}</p>
              </v-layout>
            </v-flex>
          </v-layout>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn 
            color="blue darken-1" 
            flat 
            v-on:click="dialog = false"
          >Close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </v-card>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
    name: 'PeopleDataTableLayout',
    data: () => ({
        showFirstLastPage: true,
        showCurrentPage: true,
        itemsPerPageOptions: [5, 10, 20],
        search: '',
        
        headers: [{
            align: 'left',
            sortable: false, 
            width: '1%',
            value: 'picture' },
          { text: 'Name', value: 'name'},
          { text: 'Email', value: 'email' },
          { text: 'Discipline', value: 'discipline' },
          { text: 'Job Role', value: 'jobRole' }
        ],

        dialog: false
    }),
    methods: {
      ...mapActions('employee', ['getAllEmployeeInfo']),
      showUserInfo(user){
        this.userinfo = this.getAllEmployeeInfo(user)
        setTimeout(() => {
                this.dialog = true
            }, 300)
      }
    },
    computed: {
        ...mapState('employee', ['listOfEmployees', 'employeeInfo']),
        filteredEmployees() {
          return this.listOfEmployees.filter((i) => {
            if(i.name.toLowerCase().includes(this.search.toLowerCase())){
              return i
            }
          })
        }
    }
}
</script>

<style lang="scss">

#PeopleDataTableCard{
    width:80%
}

.user-image{
  width: 45px;
  height:45px;
  
  border-radius: 50%;
  margin-left: 20px;
  margin-top: 5px;
}

#user-info{
  li{
    margin-top: -15px;
  }
}

#user-info-left{
  
  border-right: 1px solid #db4326;

  #user-info-image{
    width: 92%;
    height: auto;
  }

  #user-info-name{
    color: #db4326;
  }

  h2{
    margin: 10px 0 15px 0;
  }
  
  p{
    margin-top: -15px;
    margin-right: 15px;
    overflow-wrap: break-word;
  }
}

#user-info-middle{
  padding-top: 5px;
  padding-left: 20px;
}

#user-info-right{
  ul{
    padding-left: 0;
  }
}

</style>
