<template>
  <v-card id="PeopleTreeviewCard">
    <v-select
      :items="listOfTenants"
      item-text="country"
      item-value="tenantId"
      label="Tenants"
      v-model="tenantModel"
      v-on:change="SelectedTenant"
    ></v-select>

    <v-select
      :items="listOfCities"
      item-text="name"
      item-value="locationId"
      label="Locations"
      :disabled="!displayedLocations"
      v-model="locationModel"
      v-on:change="SelectedLocation"
    ></v-select>

    <div v-if="locationModel==null"> Select a location. </div>

    <v-treeview v-else
      :items="listOfBuildingsWithFloors"
      item-key="id"
      item-text="label"
      v-model="buildingModel"
      :selectable="true"
      class="floorTreeview"
    ></v-treeview>

    <button class="load-employees-button" @click="LoadEmployees()">LOAD EMPLOYEES</button>
  </v-card>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
    name: 'PeopleTreeviewLayout',
    data: () => ({
        tenantModel: null,
        locationModel: null,
        buildingModel: [],

        displayedLocations: true,
        displayedBuildings: true,
        displayedFloors: true
    }),
    props: {
      userLocation: {required: true}  
    },
    methods: {
        ...mapActions('employee', ['getEmployeesByFloors', 'clearEmployees', 'getUserLocation']),
        ...mapActions('buildingInformation', ['getTenants', 'getCities', 'getBuildingsWithFloors']),
        SelectedTenant() {
            this.displayedLocations = true
            this.displayedBuildings = false
            
            this.locationModel = null
            this.buildingModel = null
            this.clearEmployees()

            this.getCities(this.tenantModel)
        },
        SelectedLocation() {
            this.displayedBuildings = true

            this.buildingModel = null
            this.clearEmployees()
            
            this.getBuildingsWithFloors(this.locationModel)
        },
        SelectedBuilding() {
            if (this.buildingModel) 
                this.displayedFloors = true
        },
        LoadEmployees() {
            this.getEmployeesByFloors(this.buildingModel.filter(x => x[0]=='F'))
        }
    },
    computed: {
        ...mapState('employee', ['listOfEmployees']),
        ...mapState('buildingInformation', ['listOfTenants', 'listOfCities', 'listOfBuildingsWithFloors'])
    },
    created() {
        this.tenantModel = this.userLocation.tenantId
        this.locationModel = this.userLocation.locationId
        this.buildingModel.push(this.userLocation.floorId)
        
        this.getTenants()
        this.getCities(this.tenantModel)
        this.getBuildingsWithFloors(this.locationModel)
        this.getEmployeesByFloors(this.buildingModel)
    }
}
</script>

<style lang="scss">
@import '@/styles/global.scss';

#PeopleTreeviewCard {
    width: 20%;
    padding: 10px 15px;
    overflow: hidden;
}

.load-employees-button{
    position: absolute;
    bottom: 0;

    margin: auto;
    left: 0;
    right: 0;

    width: 95%;
    height: 40px;
    background-color: #db4326;
    color: white;
    margin: 0 2% 2% 2%;
}

</style>
