<template>
    <v-layout fluid fill-height class="mapLayout">
        <div id="map" class="innerMap"></div>
        <component v-bind:is="changePeopleComponent" v-bind:floorId="floorId" v-if="selectedDesk"></component>
    </v-layout>
</template>

<script>
import { mapService } from '@/services/map.service.js'
import { DESK } from '@/store/commonSpaces.entityTypes.js'
import * as fromService from '@/services'
import AddNewPerson from './PlacePeopleAddNew'
import ChangePerson from './PlacePeopleChange'
import { mapState, mapActions } from 'vuex'
import store from '@/store'

export default {
    components: {
        'add-new-person': AddNewPerson,
        'change-person': ChangePerson
    },
    props: {
        floorId: { required: true }
    },
    data: () => ({
        deskService: null,
        map: undefined,
        deskLayer: undefined,
        selectInteraction: undefined,
        floorIdLocal: undefined
    }),
    created() {
        this.deskService = new fromService.DeskService(store.getters['account/token'])
        this.floorIdLocal = this.floorId
        this.$root.$on('mapChange', newfloorId => {
            this.floorIdLocal = newfloorId
            this.createMap()
        })
    },
    mounted() {
        // create select interaction
        this.selectInteraction = mapService.selectShape()
        this.selectInteraction.on('select', e => {
            var selectedFeature = null
            if (e.target.getFeatures().getArray()[0]) {
                selectedFeature = e.target
                    .getFeatures()
                    .getArray()[0]
                    .getProperties()
            }
            this.newSelection(selectedFeature)
        })
        // create the map
        this.createMap()
    },
    computed: {
        ...mapState('placePeople', ['selectedDesk', 'reloadMap']),
        changePeopleComponent() {
            if (this.selectedDesk.occupied) {
                return 'change-person'
            } else {
                return 'add-new-person'
            }
        }
    },
    watch: {
        selectedDesk() {
            if (!this.selectedDesk) {
                this.selectInteraction.getFeatures().clear()
            }
        },
        reloadMap() {
            if (this.reloadMap) {
                this.map.setTarget(null)
                this.setReloadMap(false)
                this.createMap()
            }
        }
    },
    methods: {
        ...mapActions('placePeople', ['setSelectedDesk', 'setSelectedEmployeeId', 'clearSelection', 'setReloadMap']),
        ...mapActions('floorLayout', ['getLayout']),
        createMap() {
            if (this.map) {
                this.map.setTarget(null)
                this.map = null
            }
            this.getLayout(this.floorIdLocal).then(layout => {
                if (layout) {
                    this.map = mapService.createMap(layout.floorLayoutImage)
                    // get common spaces
                    this.getDeskLayers()
                    this.map.addInteraction(this.selectInteraction)
                }
            })
        },
        getDeskLayers() {
            this.deskService.getDesksForFloor(this.floorIdLocal).then(deskFeatureCollection => {
                if (this.deskLayer) {
                    this.map.removeLayer(this.deskLayer)
                    this.deskLayer = undefined
                }
                // add existing features to map
                this.deskLayer = mapService.createDeskLayer(DESK, deskFeatureCollection)
                // add the layer to map
                this.map.addLayer(this.deskLayer)
            })
        },
        newSelection(newFestureSelected) {
            this.clearSelection()
            if (newFestureSelected) {
                var newSelectedDesk = {
                    id: newFestureSelected.Id,
                    occupied: newFestureSelected.Occupied
                }
                this.setSelectedDesk(newSelectedDesk)
            }
        }
    }
}
</script>

<style lang="scss" scoped>
.mapLayout {
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}
.innerMap {
    width: 100%;
}
</style>